<template>
    <v-container id="LeitoresPage">
        <v-row>
            <v-col>
                <v-sheet rounded="xl">
                    <v-data-table
                        class="rounded-xl elevation-7"
                        :headers="headers"
                        :items="users"
                        :items-per-page="5"
                        :search="procurar"
                        :loading="items"
                        :loading-text="carregando"
                    >
                        <template v-slot:item.acoes="{ item }">
                            <v-tooltip top color="#0061A3">
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn
                                        color="primary"
                                        v-bind="attrs"
                                        v-on="on"
                                        text
                                        small
                                        rounded
                                        @click="showEdit(item)"
                                    >
                                        <v-icon dark> mdi-pencil </v-icon>
                                    </v-btn>
                                </template>
                                <span>Editar</span>
                            </v-tooltip>
                            <v-tooltip top color="red">
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn
                                        color="error"
                                        v-bind="attrs"
                                        v-on="on"
                                        text
                                        small
                                        rounded
                                        @click="remover(item)"
                                    >
                                        <v-icon dark> mdi-delete </v-icon>
                                    </v-btn>
                                </template>
                                <span>Remover</span>
                            </v-tooltip>
                        </template>
                    </v-data-table>
                </v-sheet>
            </v-col>
        </v-row>

        <v-row justify="center">
            <v-dialog v-model="dialog" persistent max-width="600px">
                <template v-slot:activator="{ on, attrs }">
                    <v-btn color="#FF0038" dark v-bind="attrs" v-on="on">
                        <font-awesome-icon icon="fa-solid fa-plus" />
                        <v-spacer></v-spacer>
                        Novo leitor
                    </v-btn>
                </template>
                <v-card>
                    <v-card-title>
                        <span class="text-h5">Leitor Info </span>
                    </v-card-title>
                    <v-card-text>
                        <v-container>
                            <v-row>
                                <v-col cols="12">
                                    <v-text-field
                                        label="Nome do leitor"
                                        append-icon="mdi-account"
                                        required
                                    ></v-text-field>
                                </v-col>
                                <v-col cols="12">
                                    <v-text-field label="Cidade" append-icon="mdi-city-variant" required></v-text-field>
                                </v-col>
                                <v-col cols="12">
                                    <v-text-field
                                        label="Endereo"
                                        append-icon="mdi-home-account"
                                        required
                                    ></v-text-field>
                                </v-col>
                            </v-row>
                        </v-container>
                        <small>*Todos os campos so obrigatrios</small>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="dialog = false"> Cancelar </v-btn>
                        <v-btn color="blue darken-1" text @click="dialog = false"> Salvar </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-row>
    </v-container>
</template>

<script>
import Users from '../service/users';
import Swal from 'sweetalert2';

export default {
    data: () => ({
        user: {
            name: '',
            city: '',
            address: ''
        },

        users: [],

        modal: false,
        dialog: false,
        search: '',

        headers: [
            { text: 'ID', value: 'id' },
            { text: 'NOME', value: 'name' },
            { text: 'CIDADE', value: 'city' },
            { text: 'ENDEREO', value: 'address' },
            { text: '', value: 'acoes', sortable: false }
        ]
    }),

    mounted() {
        Users.getAllUsers().then((response) => {
            this.users = response.data.content;
        });
    },

    methods: {
        creatUser() {
            Users.createUser;
        }
    }
};
</script>

<style>
#leitoresPage {
    background-color: #1f1f1f;
}
</style>
